.lt-boxplot-box {
  stroke: var(--current-color, black);
  vector-effect: non-scaling-stroke;
}

.lt-boxplot-frame {
  fill: none;
  stroke: var(--current-color, black);
  vector-effect: non-scaling-stroke;
}

.lt-boxplot-outlier {
  fill: none;
  stroke: var(--current-color, black);
  vector-effect: non-scaling-stroke;
}

.lt-boxplot-mean {
  fill: none;
  stroke: var(--current-color, black);
  vector-effect: non-scaling-stroke;
  stroke-dasharray: 2, 1;
}

.lt-boxplot-pre {
  opacity: 0.2;
}

.lt-boxplot {
  display: flex;
  flex-direction: column;
}

.lt-boxplot-wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}

