}

/**
 * @lineup-lite/components
 * https://github.com/sgratzl/lineup-lite
 *
 * Copyright (c) 2021 Samuel Gratzl <sam@sgratzl.com>
 */

.lt-filter-range {
  position: absolute;
  top: 0;
  height: 100%;
  background: repeating-linear-gradient(
    -45deg,
    rgba(var(--current-inverted-color-rgb, 255, 255, 255), 0.7),
    rgba(var(--current-inverted-color-rgb, 255, 255, 255), 0.7) 7px,
    rgba(var(--current-color-rgb, 0, 0, 0), 0.2) 7px,
    rgba(var(--current-color-rgb, 0, 0, 0), 0.2) 10px
  );
}

.lt-filter-range-min {
  left: 0;
}

.lt-filter-range-max {
  right: 0;
}

.lt-filter-range-drag {
  position: absolute;
  top: 0;
  height: 100%;
  cursor: ew-resize;
  opacity: 0.5;
}

.lt-filter-range-drag:hover {
  opacity: 1;
}

.lt-filter-range-min > .lt-filter-range-drag {
  border-right: 0px solid var(--current-color, black);
  transition: border-right-width 0.5s ease;
  left: 100%;
}

.lt-filter-range-max > .lt-filter-range-drag {
  border-left: 0px solid var(--current-color, black);
  transition: border-left-width 0.5s ease;
  right: 100%;
}

.lt-summary:hover > .lt-filter-range-min > .lt-filter-range-drag {
  border-right-width: 3px;
}
.lt-summary:hover > .lt-filter-range-max > .lt-filter-range-drag {
  border-left-width: 3px;
}

/**
 * @lineup-lite/components
 * https://github.com/sgratzl/lineup-lite
 *
 * Copyright (c) 2021 Samuel Gratzl <sam@sgratzl.com>
 */

.lt-histogram {
  display: flex;
  justify-content: space-evenly;
}
.lt-histogram.lt-group {
  flex-grow: 1;
}

.lt-histogram-bin {
  position: relative;
  margin-right: 1px;
  flex-grow: 1;
}

.lt-histogram-bin-interactive {
  cursor: pointer;
}

.lt-histogram-bin-interactive:hover {
  opacity: 0.8;
}

.lt-histogram-bin[data-label]::before {
  content: attr(data-label);
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  overflow: hidden;
  text-align: center;
}

.lt-histogram-bin[data-label][data-filtered='true']::before,
.lt-histogram-bin[data-label][data-filtered='mustNot']::before {
  text-decoration: line-through;
}
.lt-histogram-bin[data-label][data-filtered='must']::before {
  font-weight: bold;
}

.lt-histogram-bin-dense {
  margin-right: 0;
}

/**
 * @lineup-lite/components
 * https://github.com/sgratzl/lineup-lite
 *
 * Copyright (c) 2021 Samuel Gratzl <sam@sgratzl.com>
 */

.lt-bar,
.lt-diverging-bar {
  text-align: right;
  padding-right: 0.2em;
  text-shadow: 0 0 2px var(--current-inverted-color, white);
}

.lt-diverging-bar {
  position: relative;
}
.lt-diverging-bar::before {
  content: '';
  width: 1px;
  top: 0;
  left: 50%;
  height: 100%;
  border-left: 1px solid var(--current-color, currentColor);
  position: absolute;
}

/**
