  text-overflow: ellipsis;
}

/**
 * @lineup-lite/components
 * https://github.com/sgratzl/lineup-lite
 *
 * Copyright (c) 2021 Samuel Gratzl <sam@sgratzl.com>
 */

.lt-text-summary {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.lt-text-summary-input {
  min-width: 0;
  font-size: xx-small;
}

.lt-text-summary-clear {
  position: absolute;
  right: 0.1em;
  border: none;
  background: none;
  z-index: 1;
}

.lt-text-summary-clear:hover,
.lt-text-summary-clear:focus {
  border: none;
  outline: none;
  font-weight: bolder;
}

.lt-text-summary-clear:disabled {
  display: none;
}

/**
 * @lineup-lite/components
 * https://github.com/sgratzl/lineup-lite
 *
 * Copyright (c) 2021 Samuel Gratzl <sam@sgratzl.com>
 */

.lt-summary {
  font-size: xx-small;
  min-height: 2.5em;
  margin-bottom: 1.2em;
  position: relative;
  font-weight: normal;
}

.lt-group {
  margin-bottom: 0;
}

.lt-summary::before,
.lt-summary[data-max]::after,
.lt-summary.lt-summary-center > .lt-summary-center {
  white-space: nowrap;
  position: absolute;
  top: 100%;
  overflow: hidden;
  text-align: right;
  line-height: 1.2;
}

.lt-summary::before {
  left: 0;
  width: 100%;
  text-align: left;
  padding-left: 0.1em;
}

.lt-summary[data-min]::before {
  content: attr(data-min);
}

.lt-summary[data-max]::before {
  width: 50%;
}

.lt-summary[data-max]::after {
  content: attr(data-max);
  right: 0;
  width: 50%;
  text-align: right;
  padding-right: 0.1em;
}

.lt-summary.lt-summary-center::before {
  width: 35%;
}
.lt-summary.lt-summary-center::after {
  width: 35%;
}
.lt-summary.lt-summary-center > .lt-summary-center {
  left: 35%;
  right: 35%;
  text-align: center;
}

/**
 * @lineup-lite/components
 * https://github.com/sgratzl/lineup-lite
