 */

.lt-upset-line {
  display: flex;
  justify-content: space-evenly;
  position: relative;
  height: 1.2em;
}

.lt-upset-line > svg {
  height: 100%;
}

.lt-upset-dot {
  opacity: 0.2;
  fill: var(--current-color, currentColor);
}

.lt-upset-dot-enabled {
  opacity: 1;
}

.lt-upset-line-line {
  position: absolute;
  top: 0;
  left: 10%;
  right: 10%;
  height: 100%;
  pointer-events: none;
}

.lt-upset-line-line > path {
  fill: none;
  stroke: var(--current-color, currentColor);
  stroke-width: 0.5;
}

/**
 * @lineup-lite/components
 * https://github.com/sgratzl/lineup-lite
 *
 * Copyright (c) 2021 Samuel Gratzl <sam@sgratzl.com>
 */

.lt-heatmap-1d {
  flex-grow: 1;
  display: flex;
  height: 1.2em;
}

.lt-heatmap-1d-cell {
  flex: 1 1 0;
}

.lt-heatmap-1d-cell > [aria-hidden='false'] {
  display: none;
}

/**
 * @lineup-lite/components
 * https://github.com/sgratzl/lineup-lite
 *
 * Copyright (c) 2021 Samuel Gratzl <sam@sgratzl.com>
 */

.lt-line-chart {
  position: relative;
  padding: 2px;
}

.lt-line-chart-container {
  position: absolute;
  left: 2px;
  top: 2px;
  width: calc(100% - 4px);
  height: calc(100% - 4px);
}

.lt-line-chart-points {
  position: absolute;
  left: 2px;
  top: 2px;
  right: 2px;
  bottom: 2px;
}

.lt-line-chart-line {
  stroke: var(--current-color, black);
  vector-effect: non-scaling-stroke;
  fill: none;
}

.lt-line-chart-area {
  fill: var(--current-color, black);
  opacity: 0.25;
}

.lt-line-chart-pre {
  opacity: 0.2;
}

.lt-line-chart-area-pre {
  opacity: 0.05;
}

.lt-line-chart-point {
  position: absolute;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  transform-origin: center center;
  background-color: var(--current-color, black);
}

