/**
 * @lineup-lite/hooks
 * https://github.com/sgratzl/lineup-lite
 *
 * Copyright (c) 2021 Samuel Gratzl <sam@sgratzl.com>
 */

.lt-expand-agg {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  transition: transform 0.25s ease;
  opacity: 0.25;
  color: var(--current-color, currentColor);
  border: none;
  background: none;
  padding: 0;
}

.lt-expand-agg > svg {
  fill: var(--current-color, currentColor);
}

.lt-expand-agg:hover,
.lt-expand-agg:active,
.lt-expand-agg:focus {
  background: none;
}

.lt-expand-agg:hover,
.lt-expand-agg:active,
.lt-expand-agg:focus {
  opacity: 1;
}

.lt-expanded {
  transform: rotate(90deg);
}
.lt-expand-1 {
  transform: translate(2px, 0);
}
.lt-expand-1.lt-expanded {
  transform: translate(2px, 0) rotate(90deg);
}
.lt-expand-2 {
  transform: translate(4px, 0);
}
.lt-expand-2.lt-expanded {
  transform: translate(4px, 0) rotate(90deg);
}
.lt-expand-3 {
  transform: translate(6px, 0);
}
.lt-expand-4.lt-expanded {
  transform: translate(6px, 0) rotate(90deg);
}

/**
 * @lineup-lite/hooks
 * https://github.com/sgratzl/lineup-lite
 *
 * Copyright (c) 2021 Samuel Gratzl <sam@sgratzl.com>
 */

.lt-rank {
  text-align: right;
  white-space: nowrap;
}

.lt-rank-agg {
  flex-grow: 1;
  text-align: right;
  white-space: nowrap;
  display: flex;
  flex-direction: column;
}

.lt-rank-agg-spacer {
  flex-grow: 1;
  flex-basis: 0.2em;
  border-right: 1px solid var(--current-color, currentColor);
  margin-right: 0.4em;
}

/**
 * @lineup-lite/hooks
 * https://github.com/sgratzl/lineup-lite
 *
 * Copyright (c) 2021 Samuel Gratzl <sam@sgratzl.com>
 */

.lt-missing {
  background-image: linear-gradient(180deg, #c1c1c1 3px, transparent 3px);
  background-position: center center;
  background-size: 10px 3px;
  background-repeat: no-repeat;
  min-height: 1.3em;
  color: transparent;
}


/**
 * @lineup-lite/table
 * https://github.com/sgratzl/lineup-lite
