 */

.lt-table {
}

.lt-colors {
  --current-color: currentColor;
  --current-color-rgb: 0, 0, 0;
  --current-inverted-color-rgb: 255, 255, 255;
  --current-inverted-color: rgb(var(--current-inverted-color-rgb));
  --select-color: #ffa500;
}
.lt-dark {
  --current-color-rgb: 255, 255, 255;
  --current-inverted-color-rgb: 0, 0, 0;
  --select-color: #ff8c00;
}
.lt-table-virtual {
  display: flex;
  flex-direction: column;
}

.lt-thead {
  margin-bottom: 0.25em;
}

.lt-thead-virtual {
  overflow: hidden;
}

.lt-th {
  display: inline-flex;
  flex-direction: column;
  justify-content: space-between;
}

.lt-th-group {
}

.lt-tbody {
}

.lt-tbody-virtual {
  flex: 1 1 0;
  overflow: auto;
}

.lt-tr {
}

.lt-tr-virtual {
  position: absolute;
  left: 0;
  top: 0;
}

/* .lt-tr-selected {
  box-shadow: 0 0 0 2px var(--select-color, #ffa500);
} */

.lt-th,
.lt-td {
  margin: 0 0.25em;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.lt-td {
  display: inline-flex !important;
  flex-direction: column;
  justify-content: center;
}

.lt-th-support,
.lt-td-support {
  margin: 0;
}

.lt-header {
  text-align: center;
}

.lt-column-resize-handle {
  position: absolute;
  top: 0;
  right: -0.25em;
  width: 0.5em;
  height: 100%;
  background: var(--current-color, black);
  cursor: col-resize;
  transform: translateX(50%);
  /* prevents from scrolling while dragging on touch devices */
  touch-action: none;

  transition: opacity 0.25s ease;
  opacity: 0;
}

.lt-th:hover > .lt-column-resize-handle,
.lt-column-resizing > .lt-column-resize-handle {
  z-index: 1;
  opacity: 0.1;
}

.lt-summary:not(.lt-group)::before {
  /** always have a content */
  content: attr(data-min) '';
  border-top: 1px solid var(--current-color, currentColor);
}

.lt-summary[data-max]::before {
  border-left: 1px solid var(--current-color, currentColor);
  border-image: linear-gradient(
    to bottom,
    var(--current-color, currentColor) 0%,
    var(--current-color, currentColor) 50%,
    transparent 50%
  );
  border-image-slice: 1 1;
}

.lt-summary[data-max]::after {
  border-top: 1px solid var(--current-color, currentColor);
  border-right: 1px solid var(--current-color, currentColor);
  border-image: linear-gradient(
    to bottom,
    var(--current-color, currentColor) 0%,
    var(--current-color, currentColor) 50%,
    transparent 50%
  );
  border-image-slice: 1 1;
}

.lt-summary.lt-summary-center > .lt-summary-center {
  border-top: 1px solid var(--current-color, currentColor);
}

/**
 * @lineup-lite/table
 * https://github.com/sgratzl/lineup-lite
 *
 * Copyright (c) 2021 Samuel Gratzl <sam@sgratzl.com>
 */

.lt-toolbar {
  display: flex;
  justify-content: center;
  position: relative;
}

.lt-action {
  position: relative;
  margin: 0.1em 0.2em;
  opacity: 0.25;
  transition: opacity 0.25s ease;
  font-size: inherit;
  box-sizing: border-box;
  border: none;
  background: none;
  padding: 0;
  color: var(--current-color, currentColor);
  cursor: pointer;
}

.lt-action[data-index]::before {
  content: attr(data-index);
  font-size: 70%;
  line-height: 1;
  position: absolute;
  left: 100%;
  top: -2px;
}

.lt-action > svg {
  fill: var(--current-color, currentColor);
}

.lt-action:hover,
.lt-action:active,
.lt-action:focus {
  background: none;
}

.lt-action:hover,
.lt-action-active {
  opacity: 1;
}

/**
 * @lineup-lite/table
 * https://github.com/sgratzl/lineup-lite
 *
 * Copyright (c) 2021 Samuel Gratzl <sam@sgratzl.com>
 */

.lt-panel {
  display: flex;
  flex-direction: column;
}

.lt-data-summary {
  text-align: left;
  margin: 0.5em;
}

.lt-data-summary-selected {
  /*box-shadow: 0 0 0 2px var(--select-color, #ffa500);*/
}

.lt-text-action {
  display: inline-block;
  text-align: left;
  font: inherit;
  font-size: inherit;
  box-sizing: border-box;
  border: none;
  background: none;
  padding: 0;
  color: var(--current-color, currentColor);
  cursor: pointer;
}

.lt-text-action > svg {
  margin-left: 0.2em;
  fill: var(--current-color, currentColor);
  width: 0.8em;
  height: auto;
  opacity: 0.25;
  transition: opacity 0.25s ease;
}

.lt-text-action:hover,
.lt-text-action:active,
.lt-text-action:focus {
  background: none;
}

.lt-text-action:hover > svg,
.lt-text-action-active > svg {
  opacity: 1;
}

.lt-table-summary {
  flex: 1 1 0;
  position: relative;
}

.lt-table-summary > div {
  position: absolute;
  overflow: auto;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.lt-panel-th {
  margin: 0.5em 0.5em 0.5em 0.5em;
  padding-top: 1em;
  border-top: 1px solid var(--current-color, currentColor);
}

.lt-panel-th > .lt-summary.lt-histogram {
  min-height: 5em;
}

.lt-panel-th-container {
  display: flex;
  align-items: flex-start;
}

.lt-panel-header {
  flex: 1 1 0;
  overflow: hidden;
  text-overflow: ellipsis;
}

